.hero {
  @include section(0, 150);
  display: flex;
  justify-content: center;
}

.hero__container {
  @extend .container;
  border-radius: 10px;

  @media screen and (min-width: 1920px) {
    height: 600px;
    max-width: 1296px;
    padding-top: 136px;
    padding-bottom: 210px;
    background-image: url(../images/hero/hero-img.jpg);
    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/hero/hero-img@2x.jpg);
    }
    @media (min-device-pixel-ratio: 3),
      (-webkit-min-device-pixel-ratio: 3),
      (min-resolution: 288dpi),
      (min-resolution: 3dppx) {
      background-image: url(../images/hero/hero-img@3x.jpg);
    }
  }
}

.hero__wrap {
  text-align: center;
}

.hero__uppertitle {
  font-weight: 700;
  font-family: var(--secondary-font);
  font-size: 24px;
  margin-bottom: 40px;
}

.hero__icon {
  fill: #fff;

  &:first-child {
    margin-right: 66px;
  }

  &:last-child {
    margin-left: 66px;
  }
}

.hero__title {
  @extend %main-title;
  max-width: 587px;
  margin: 0 auto;
}
